import React, { Component } from 'react';
import { restdb, realtimeURL } from './helper.js';
import FadeIn from 'react-fade-in';
import axios from 'axios';
import Map2 from './Map2';
import Modal from 'react-modal';

class Comments extends Component {

  constructor(props) {
    super(props);
    this.state = {
      komentarai: [],
      loading: true,
      model: {
              showModal: false,
              name: "",
              Koordinates: ""
              }
    }

    let eventSource = new EventSource(realtimeURL);

    eventSource.addEventListener('post', (e) => {


        var fulldata = JSON.parse(e.data);
        var data = fulldata.data;
        delete data._version;
        delete data.data;
        var today = Math.floor(Date.now() / 1000)
        data['data'] = today;
        this.newComment(data)

    }, false);


  }

  componentDidMount() {

    this.uploadAllComments();
  }

  loading = () => {
    this.setState({komentarai: this.state.komentarai, loading: true})
  }

  newComment(data){

    var allStatuses = this.state.komentarai;
    allStatuses.unshift(data);
    this.setState({komentarai: allStatuses, loading: false});

  }


  uploadAllComments = () => {

    this.setState({ komentarai: this.state.komentarai, loading: true });

      restdb.get("/rest/komentarai")
      .then(res => {
        var komentai = res.data;
        komentai.sort(function(b, a){return a.data - b.data });
        this.setState({ komentarai: komentai, loading: false });
     });
  }

  openMap = (stotele) => {

      this.setState({ komentarai: this.state.komentarai, loading: this.state.loading, model: {showModal: true, name: stotele.tagas, Koordinates: stotele.Koordinates} });

  }

  closeMap = () => {

      this.setState({ komentarai: this.state.komentarai, loading: this.state.loading, model: {showModal: false, name: this.state.model.name, Koordinates: this.state.model.Koordinates} });

  }

  render() {

    const customStyles = {
      content : {
        top                   : '50%',
        left                  : '50%',
        right                 : 'auto',
        bottom                : 'auto',
        marginRight           : '-50%',
        transform             : 'translate(-50%, -50%)',
        zIndex                : 3
      },
      overlay: {zIndex: 10}
    };


    var mapas =
    <Modal
    isOpen={this.state.model.showModal}
    contentLabel="Example Modal"
    style={customStyles}
    >
      <div className="text-right">
        <span onClick={this.closeMap} className="text-dark h1 font-weight-bold" id="exit" style={{lineHeight: 0, cursor: 'default'}}>&times;</span>
      </div>
      <Map2 vardas={this.state.model.name} koordinates={this.state.model.Koordinates} />
      <div className="text-center">
        <p className="text-dark h6 font-weight-bold " style={{lineHeight: 1.5}}>{this.state.model.name}</p>
      </div>
    </Modal>;

    function laikas(paskelbta){
        var dataNow = Math.floor(Date.now() / 1000);
        var res = Math.abs(dataNow - paskelbta);
        //var sekundes = res % 60;
        var minutes = Math.floor(res / 60) % 60;
        var valandos = Math.floor(res / 3600) % 24;
        //var dienos = Math.floor(res / 86400);

       return valandos + ' h ' + minutes + ' min ';
    };


    var listItems;
    var loading;

    if (this.state.loading){
      loading =
      <center><img src="/css/loader2.gif" alt="" /></center>;
    }

      listItems = this.state.komentarai.map((vardas) =>

      <div className="container-fluid bg-light rounded mb-2 border fade-in" key={vardas._id}>

            <div className="text-wrap h6 text-muted m-2">{vardas.vardas}</div>

            <div className="container-fluid rounded bg-white border border-ligth p-0 text-secondary text-justify p-1 text-wrap" style={{minHeight:80}}>
               {vardas.komentaras}
             </div>

             <label className="mr-2">Sotelė:</label>
             <button className="btn btn-warning pt-0 pb-0 m-2" onClick={this.openMap.bind(this, vardas)}>{vardas.tagas}</button>
             <label className="mr-2 d-inline-block">Paskelbta prieš:</label><h6 className="text-muted d-inline-block">{ laikas(vardas.data) }</h6>

        </div>


      );

    return (

    <div className="m-2">
    {loading}
    {listItems}


    {mapas}


    </div>
    );

  }
}

export default Comments;
