import React, { Component } from 'react';

class TextInput extends Component {
  constructor(props) {
    super(props);
    this.state =
    {
        komentaras: [{name: "", comment: ""}],
        lng: 0
    };

    this.hanldeVardas = this.hanldeVardas.bind(this);
    this.handleKomentaras = this.handleKomentaras.bind(this);

  }


  hanldeVardas(event){
    var komentaras = this.state.komentaras[0].comment;
    var lng = this.state.lng;

    this.setState(
    {
        komentaras: [{name: event.target.value, comment: komentaras}],
        lng: lng
    }
    );

    this.props.komentaras(komentaras, event.target.value);
  };

  handleKomentaras(event){

    var vardas = this.state.komentaras[0].name;
    var lng = event.target.value.length;

    this.setState(
    {
        komentaras: [{name: vardas, comment: event.target.value}],
        lng: lng
    }
    );

    this.props.komentaras(event.target.value, vardas);
  };

  render() {

    return (


        <form className="px-2  py-1">
          <div className="form-group mb-2">
            <label>Vardas:</label>
            <input onChange={this.hanldeVardas} className="form-control w-30" placeholder="Irašykite vardą" />
          </div>
          <div className="form-group  pb-0 mb-0">
            <label>Komentaras: [{300 - this.state.lng}]</label>
            <textarea maxLength="300" onChange={this.handleKomentaras} placeholder="Irašykite komentarą" className="form-control w-30" id="exampleFormControlTextarea1" rows="3" />
          </div>
        </form>


    );
  }
}
export default TextInput;
