import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Map, TileLayer, Marker, Popup, CircleMarker, Tooltip, Pane, Rectangle } from 'react-leaflet';
import axios from 'axios';
import { restdb, realtimeURL } from './helper.js';
import L from 'leaflet';
import MapInfo from "./MapInfo";
import './mapinfo.css';

export default class Map3 extends Component {

  constructor(props){
    super(props);
    this.state = {
      komentarai: [],
      loading: true
    }
  }

  componentDidMount() {

      restdb.get("/rest/komentarai")
      .then(res => {
        var komentai = res.data;
        komentai.sort(function(b, a){return a.data - b.data });
        this.setState({ komentarai: komentai, loading: false });
     });

  }


  render() {

    var loading;
    var listItems;


      loading =
      <center><img src="/css/loader2.gif" alt="" /></center>;


    function koordinate(esama){
      const position = esama;
      var res = position.split(", ");
      var result = [];
      result[0] = Number(res[0]);
      result[1] = Number(res[1]);
      return result;
    }

    const iconPerson = new L.Icon({
    iconUrl: require('./car.png'),
    iconRetinaUrl: require('./car.png'),
    iconAnchor: null,
    shadowUrl: null,
    shadowSize: null,
    shadowAnchor: null,
    iconSize: [32,32],
    });



    listItems = this.state.komentarai.map((vardas) =>
      <Marker position={koordinate(vardas.Koordinates)} icon={ iconPerson }>
        <Popup>
          {vardas.tagas} <br />
          Paskelbta prieš: {laikas(vardas.data)}
        </Popup>
      </Marker>
    );

    function laikas(paskelbta){
        var dataNow = Math.floor(Date.now() / 1000);
        var res = Math.abs(dataNow - paskelbta);
        //var sekundes = res % 60;
        var minutes = Math.floor(res / 60) % 60;
        var valandos = Math.floor(res / 3600) % 24;
        //var dienos = Math.floor(res / 86400);

       return valandos + ' h ' + minutes + ' min ';
    };



    var vilnius = [54.677778, 25.291667];

    return (

      <React.Fragment>

      {this.state.loading ? loading :

      <Map center={vilnius} zoom={12} zoomControl={true} viewport={vilnius} style={{width: "100%", height: "100%", zIndex: "0"}}>
        <MapInfo />
        <TileLayer style={{all: "revert"}}
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />

        {listItems}

      </Map>}

      </React.Fragment>
    )
  }
}
